







import blockMixin from './../BlockMixin.vue'

export default {
  name: 'heading-mixin'

  mixins: [blockMixin]

  data: -> {
    fields: {
      content: ''
    }
  }

  mounted: ->
    this.$el.firstChild.focus() if this.block.fields.content.length <= 0

  methods: {
    removeBlock: (event) ->
      return if event.target.value.length > 0
      this.$emit('remove-block', this.index)
  }
}



